body {
  margin: 0;
  padding: 0;
  background-color: #e6e6fa; /* 背景色を指定 */
  font-family: Arial, sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgba(189, 182, 182, 0.813);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.app-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
}

h1 {
  margin-bottom: 40px;
  color: #333;
}

.content-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form {
  width: 100%;
  max-width: 600px;
  margin-bottom: 40px;
}

.form label {
  display: block;
  margin-bottom: 10px;
  font-weight: bold;
  text-align: left;
  color: #555;
}

.form textarea {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  resize: vertical;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.form-buttons {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
}

.form button {
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  background-color: #1976d2;
  color: #fff;
  transition: background-color 0.3s ease;
}

.form button:hover {
  background-color: #1565c0;
}

.viewer-container {
  width: 100%;
  max-width: 800px;
  height: 600px;
  border: 1px solid #ccc;
  border-radius: 4px;
  position: relative;
}

.viewer {
  width: 100%;
  height: 100%;
}